import React from "react";
import { useMsal } from "@azure/msal-react";
import DropdownButton from "react-bootstrap/DropdownButton";
import Dropdown from "react-bootstrap/esm/Dropdown";

/**
 * Renders a sign-out button
 */
export const SignOutButton = () => {
    const { instance } = useMsal();

    const handleLogout = (logoutType) => {
        if (logoutType === "popup") {
            instance.logoutPopup({
                postLogoutRedirectUri: "https://finn1018.github.io/OAuth-test-app/",
                mainWindowRedirectUri: "https://finn1018.github.io/OAuth-test-app/"
            });
        } else if (logoutType === "redirect") {
            instance.logoutRedirect({
                postLogoutRedirectUri: "https://finn1018.github.io/OAuth-test-app/",
            });
        }
    }
    return (
        <DropdownButton variant="secondary" className="ml-auto" drop="left" title="로그아웃">
            <Dropdown.Item as="button" onClick={() => handleLogout("popup")}>팝업</Dropdown.Item>
            <Dropdown.Item as="button" onClick={() => handleLogout("redirect")}>리다이렉트</Dropdown.Item>
        </DropdownButton>
    )
}