import React, { useState } from "react";
import { AuthenticatedTemplate, UnauthenticatedTemplate, useMsal } from "@azure/msal-react";
import { loginRequest } from "./authConfig";

import { PageLayout } from "./components/PageLayout";
import { callMsGraph, callMsGraphOrganization, callMsGraphContracts, callPartnerOrganization} from "./graph";
import Button from "react-bootstrap/Button";
import "./styles/App.css";


/**
 * Renders information about the signed-in user or a button to retrieve data about the user
 */
const ProfileContent = () => {
    const { instance, accounts } = useMsal();
    const [graphData, setGraphData] = useState(null);
    const [graphDataOrganization, setGraphDataOrganization] = useState(null);
    const [graphDataContracts, setGraphDataContracts] = useState(null);
    const [test, setTest] = useState(null);

    function RequestProfileData() {
        instance.acquireTokenSilent({
            ...loginRequest,
            account: accounts[0]
        }).then((response) => {
            callMsGraph(response.accessToken).then(response => setGraphData(response));
        });
    }
    function RequestProfileDataOrganization() {
        instance.acquireTokenSilent({
            ...loginRequest,
            account: accounts[0]
        }).then((response) => {
            callMsGraphOrganization(response.accessToken).then(response => setGraphDataOrganization(response));
        });
    }

    function RequestProfileDataContracts() {
        instance.acquireTokenSilent({
            ...loginRequest,
            account: accounts[0]
        }).then((response) => {
            callMsGraphContracts(response.accessToken).then(response => setGraphDataContracts(response));
        });
    }

    function RequestPartnerOrganization() {
        instance.acquireTokenSilent({
            ...loginRequest,
            account: accounts[0]
        }).then((response) => {
            callPartnerOrganization(response.accessToken).then(response => setTest(response));
        });
    }

    const contracts = () => graphDataContracts.value.map((v,i)=> {
            return (
                <li key={i}>
                    {i}.<br />
                    DisplayName: {v.displayName}<br/>
                    Default Domain Name: {v.defaultDomainName}<br/>
                    ID: {v.id}<br/>
                    Customer ID: {v.customerId}
                    <br/><br/>
                </li>
            )
    }) 

    return (
        <>
            <h5 className="card-title">환영합니다. {accounts[0].name} 님!</h5>
            <div>
            {graphData 
            ? <ul style={{padding: "0px"}}>
                <li style={{listStyleType: "none"}}>Display Name: {graphData.displayName}</li>
                <li style={{listStyleType: "none"}}>Sur Name: {graphData.surname}</li>
                <li style={{listStyleType: "none"}}>Given Name: {graphData.givenName}</li>
                <li style={{listStyleType: "none"}}>Mail: {graphData.mail}</li>
                <li style={{listStyleType: "none"}}>ID: {graphData.id}</li>
                </ul>
                :<Button variant="secondary" onClick={RequestProfileData}>내 계정 정보 보기</Button>
            }
            </div>
            <br/>
            <div>
                {graphDataOrganization
                ?<ul style={{padding: "0px"}}>
                    <li style={{listStyleType: "none"}}>Tenant ID: {graphDataOrganization.value[0].id}</li>
                    <li style={{listStyleType: "none"}}>Display Name: {graphDataOrganization.value[0].displayName}</li>
                    <li style={{listStyleType: "none"}}>Initial Domain: {graphDataOrganization.value[0].verifiedDomains[0].name}</li>
                    <li style={{listStyleType: "none"}}>Primary Domain: {graphDataOrganization.value[0].verifiedDomains[1].name}</li>
                    
                </ul>
                :<Button variant="secondary" onClick={RequestProfileDataOrganization}>조직 정보 보기</Button>
                }
            </div>
            <br/>
            <div>
                {graphDataContracts
                ?<ul style={{padding: "0px"}}>
                    {contracts()}
                </ul>
                :<Button variant="secondary" onClick={RequestProfileDataContracts}>고객 정보 보기</Button>
                }
            </div>
            <br/>
            <div>
                {test
                ?<ul style={{padding: "0px"}}>
                    
                </ul>
                :<Button variant="secondary" onClick={RequestPartnerOrganization}>test</Button>
                }
            </div>
        </>
    );
};


const MainContent = () => {    
    return (
        <div className="App">
            <AuthenticatedTemplate>
                <ProfileContent />
            </AuthenticatedTemplate>

            <UnauthenticatedTemplate>
                <h5 style={{fontFamily:"nanusquare", fontWeight:600, marginTop:"12%", fontSize:"15px",color:"gray"}}className="card-title">로그인을 완료해 주세요!</h5>
            </UnauthenticatedTemplate>
        </div>
    );
};

export default function App() {
    return (
        <PageLayout>
            <MainContent />
        </PageLayout>
    );
}
